import Header from "../Header";

const Layout = ({ children }) => {
  return (
    <div className=" text-purple-50 min-h-screen min-w-full bg-gradient-to-br from-indigo-500 to-indigo-900 ">
      <Header />
      <div className="p-5 grid grid-cols-1 justify-center items-center h-rest w-full       ">
     {children}
     </div>
    </div>
  );
};

export default Layout;
