import { useContext, useState } from "react";
import { Transition } from "@headlessui/react";
import { QueriesContext } from "../../../QueriesContext";
import TableLayout from "../../Table/TableLayout";
import HeaderTable from "../../Table/HeaderTable";
import SortHeader from "../../Card/SortHeader";
import OperationItem from "../../Table/OperationItem";
import Pagination from "../../Table/Pagination";
import OperationsCards from "../../Card/OperationsCards";
import SkeletonCards from "../../Loaders/SkeletonCards";
import Spinner from "../../Loaders/Spinner";

const OperationsList = ({ ...props }) => {
  const { operationList, onDelete, onEdit, cantOperations, isLoading } = props;

  const [toggleSort, setToggleSort] = useState({
    concept: false,
    category: false,
    amount: false,
    type: false,
    date: false,
  });
  const { setHistorySearchParams, querys, search, from, to } = useContext(QueriesContext);

  const handleToggleSort = (e) => {
    e.preventDefault();
    const name = e.currentTarget.getAttribute("name") || "concept";
    let sortString = "";
    toggleSort[name]
      ? (sortString = name + ":ASC")
      : (sortString = name + ":DESC");
    querys.get("sort")
      ? querys.get("sort").includes(name)
        ? querys.set("sort", querys.get("sort").split(name)[0] + sortString)
        : querys.set("sort", querys.get("sort") + "," + sortString)
      : querys.set("sort", sortString);
    toggleSort[name] = !toggleSort[name];
    setToggleSort(toggleSort);
    setHistorySearchParams(querys);
  };

  const handleResetSort = (e) => {
    let name = e.currentTarget.getAttribute("name") || "concept";
    let sort = querys.get("sort");
    let regex = new RegExp(`,*${name}:ASC,*|,*${name}:DESC,*`, "i");
    !sort?.split(",")[0]?.search(regex) ||
    !sort?.split(",")[sort?.split(",").length - 1]?.search(regex)
      ? (sort = sort?.replace(regex, ""))
      : (sort = sort?.replace(regex, ","));
    querys.set("sort", sort);
    !sort && querys.delete("sort");
    toggleSort[name] = false;
    setToggleSort(toggleSort);
    setHistorySearchParams(querys);
  };

  return (
    <>
      {cantOperations > 0 || isLoading ? (
        <>
          {isLoading ? (
            <>
              <div className=" grid justify-center text-indigo-500 w-full">
                <div className=" h-14 w-14 hidden sm:block">
                  <Spinner />
                </div>
              </div>
            </>
          ) : (
            <>
              <Transition
                as="div"
                appear={true}
                show={true}
                enter="ease-in-out duration-500 delay-150"
                enterFrom=" transform opacity-20 translate-x-10"
                enterTo=" transform"
              >
                <TableLayout>
                  <HeaderTable
                    handleResetSort={handleResetSort}
                    handleToggleSort={handleToggleSort}
                    toggleSort={toggleSort}
                  />
                  <OperationItem
                    operationList={operationList}
                    onEdit={onEdit}
                    onDelete={onDelete}
                  />
                </TableLayout>
              </Transition>
            </>
          )}
          <SortHeader
            handleResetSort={handleResetSort}
            handleToggleSort={handleToggleSort}
            toggleSort={toggleSort}
          />
          {isLoading ? (
            <SkeletonCards />
          ) : (
            <OperationsCards
              operationList={operationList}
              onEdit={onEdit}
              onDelete={onDelete}
            />
          )}
          <Pagination cantOperations={cantOperations} />
        </>
      ) : (
        <div className="grid w-full justify-center">
          {(search || from || to)  ? (
            <div className="grid gap-5 max-w-max p-5 text-indigo-900">
              <p className="text-center text-lg font-semibold">
                Nothing to show
              </p>
            </div>
          ) : (
            <div className=" grid gap-5 max-w-max p-5 text-indigo-900">
              <p className="text-center text-lg font-semibold">
                You dont have operations yet
              </p>
              <p className="text-sm text-center font-medium">
                Please add a new operation
              </p>
            </div>
          )}
        </div>
      )}
    </>
  );
};

export default OperationsList;
